import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { FrontendLogger } from '@biz-nest/core-client';
import { EventbusService } from '@front-shared-lib';
import { runSubscriptionsForSharedUse } from '@main-shared-lib';
import { NgxPopperjsDirective, NgxPopperjsModule, NgxPopperjsPlacements } from 'ngx-popperjs';
import { TooltipService } from '../../services/tooltip.service';

export interface ItooltipNavPops {
  button: boolean;
  text: string;
  route: string;
}
@Component({
  selector: 'lib-tooltip',
  standalone: true,
  imports: [NgxPopperjsModule],
  templateUrl: './tooltip.component.html',
  styleUrl: './tooltip.component.scss'
})
export class TooltipComponent
implements OnInit, OnDestroy {
  @Input() popper = '';
  @Input() popperContent = '';
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  @Input() popperTrigger: /* NgxPopperjsTriggers */ any = 'hover';
  @Input() popperPlacement: NgxPopperjsPlacements = NgxPopperjsPlacements.BOTTOM;
  @Input() popperApplyClass = 'poper-secondary';
  @Input() navProp?: ItooltipNavPops;
  @Input() popperDisabled = false;
  @Input() popperShowOnStart!: boolean;
  @Output() popperOnShown: EventEmitter<NgxPopperjsDirective> = new EventEmitter<NgxPopperjsDirective>();

  cleanup?: () => void;

  constructor(
    private tooltipService: TooltipService,
    private eventbus: EventbusService
  ) {}

  ngOnInit(): void {
    if (!this.popperDisabled) {
      this.popperDisabled = !this.tooltipService.globallyEnabled;
    }

    if (this.popperShowOnStart === undefined) {
      this.popperShowOnStart = this.tooltipService.popperShowOnStart || false;
    }

    FrontendLogger.debug('SHOW POPER ON START', this.popperShowOnStart);

    const handlers = {
      popperDisabled: (data: boolean) => {
        this.popperDisabled = data;
      },
      popperShowOnStart: (data: boolean) => {
        this.popperShowOnStart = data;
        FrontendLogger.debug('popperShowOnStart', this.popperShowOnStart);
      }
    };

    const { cleanup } = runSubscriptionsForSharedUse(this.eventbus, handlers);

    this.cleanup = cleanup;
  }

  onShown(ev) {
    this.popperOnShown.emit(ev);
  }

  ngOnDestroy() {
    this.cleanup?.();
  }
}
